.app {
  margin-left: auto;
  margin-right: auto;
  max-width: 70rem;

  background-color: var(--color-gray);

  .book-panel-container {
    color: black;
    padding: 2rem;
  }

  .title {
    font-size: 36pt;
    line-height: 3rem;
    margin-bottom: 1rem;
  }

  .navbar {
    a {
      color: initial;
    }
  }

  .spacing-div-for-banner {
    @media(max-width: 800px) {
      // When on a narrow screen...

      // We use padding for this to avoid margin collapse.
      padding-bottom: var(--basket-height);
    }
  }
}