.button {
  display: flex;
  justify-content: center;
  align-items: center;

  font-weight: bold;
  text-align: center;
  cursor: pointer;

  a {
    padding: 0.5rem;
    width: 100%;
    height: 100%;

    text-decoration: none;
    color: initial;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 0.5rem;
  }
}