.banner {
  background-color: var(--color-blue);
  color: black;

  border: 1px solid black;
  border-radius: 0.25rem;

  // Necessary because we want it to appear on top of other elements even if they're opaque.
  // https://stackoverflow.com/questions/16982345/elements-with-transparency-appearing-over-opaque-foreground-element
  z-index: 1;

  position: fixed;
  height: calc(var(--basket-height) + 1.5rem);
  padding-bottom: 1rem;

  right: 0;
  top: 0;

  width: 20rem;
  @media(max-width: 800px) {
    // When on a narrow screen.
    width: 100%;
  }

  .book-in-basket-link {
    font-weight: bold;
    text-decoration: none;
    color: initial;

    &:hover {
      color: white;
    }
  }
}