.select-a-book {
  .grid {
    display: grid;
    justify-items: center;
    grid-template-columns: 23rem 23rem 23rem;
  }

  @media(max-width: 800px) {
    // On a narrow screen...

    .grid {
      grid-template-columns: 100%; // Just one column.
    }
  }

  .cover-container {
    width: 20rem;
    height: 40rem;

    position: relative;
  }

  .cover {
    width: 100%;
    height: 80%;
    box-sizing: border-box;
    box-shadow: 10px 5px 10px black;
  }

  .read-more-options {
    background-color: var(--color-gray);
    color: black;
    padding: 1rem;

    // 18rem is the cover width; also see BookPanel.scss.
    width: calc(18rem - 0.25rem - 0.25rem);

    display: grid;
    grid-template-columns: 45% 45%;
    grid-column-gap: 1rem;
    justify-content: center;
  }
}