.book-panel {
  .cover-container {
    margin: 0 auto;
  }

  .cover {
    width: 22rem;
  }

  .pull-quote-container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    margin-left: 3rem;
    margin-right: 2rem;

    line-height: normal;
  }

  @media(max-width: 800px) {
    /* When on narrow screen... */

    /* Stack the top quote and the cover image on top of each other rather than side-to-side. */
    .cover-and-quote-container {
      flex-wrap: wrap;
    }
    .pull-quote-container {
      margin-top: 3rem;
      margin-left: 0;
      margin-right: 0;
    }
    .summary-container {
      margin-top: 3rem;
    }

    .cover {
      /* Make the image of the cover smaller. */
      // If you change this, also see SelectABook.scss.
      width: 18rem;      
    }
  }
}